import React from 'react'
import { Link } from 'react-router-dom';
import image from '../assets/peerpower.jpeg';
import "./NavBar.css";
function NavBar() {
    function myFunction() {
        var x = document.getElementById("myTopnav");
        if (x.className === "topnav") {
          x.className += " responsive";
        } else {
          x.className = "topnav";
        }
      }


    return (
        <div >
          
<div className="topnav" id="myTopnav">
  <Link to="/" className="active">Home</Link>
  <a href="#news">Explore</a>
  <a href="#contact">Career</a>
  <a href="#about">Community</a>
  <a href="#about">Events</a>
  <a href="about">About Us</a>
  <a href="#about">Donate Us</a>
  <a href="#about">Community</a>
  <a href="javascript:void(0);" className="icon"  onClick={()=>myFunction()}>
    <i className="fa fa-bars"></i>
  </a>
</div>

<div style={{backgroundImage: `url(" https://www.merginghearts.org/wp-content/uploads/2019/06/PC-Tech-class-.jpg" ) ` ,backgroundRepeat:'no-repeat', backgroundSize:" 100% 100%"}}>
  
</div>


          
        </div>
    )
}

export default NavBar
